var staticRenderFns = [function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"container-fluid mt-4"},[_c('h5',[_vm._v(" Study Title: Effects of loss aversion on attention and cognitive performance ")]),_c('h6',{staticClass:"mt-3"},[_vm._v("DEBRIEFING FORM")]),_c('p',{staticClass:"text-left mt-5"},[_vm._v(" Thank you for participating in this study conducted by Sumaiya Muhammad. We hope you enjoyed the experience. This form provides background about my research and elaborates on further details of the study. Please feel free to ask any questions or to comment on any aspect of the study. ")]),_c('p',{staticClass:"text-left"},[_vm._v(" The true purpose of the study was to determine the effects of loss aversion on your attention and performance in the task. Loss aversion is an idea introduced by Daniel Kahneman and Amos Tversky. It refers to peoples tendencies to prefer avoiding losses to earning equivalent gains. I hoped to study whether loss aversion is merely limited to preference or if it had effects on ones attention as well. Participants were thus divided into four groups for this study. All participants played the same game of pressing the space bar when an X appears on the screen. However, the instructions provided to participants were different. Group 1 was told to press the button with no mention of losses or gains. Participants in group 2 were told they would lose 10 points for each time they failed to press the space bar when the X appeared on the screen. Group 3 participants were told they would earn 10 points for each time they correctly pressed the space bar for X. In Group 4, participants were told they would earn 10 points for each correct button press and lose 10 points for incorrect button presses. ")]),_c('p',{staticClass:"text-left"},[_vm._v(" To protect the integrity of this research, I could not fully divulge the details of this study in the beginning. I withheld information from you during your participation because I did not want my predictions about the research to affect how you performed in the task. It is important that you do not share information about this project with your friends or classmates. The main reason for this is that your comments could influence the expectations, and therefore, the performance of a future participant, which would bias the data. I hope you will support my research by keeping your knowledge of this study confidential. ")]),_c('p',{staticClass:"text-left"},[_vm._v(" As you know, your participation in this study is voluntary. If you wish, you may withdraw after reading this debriefing form, at which point all records of your participation will be destroyed. You will not be penalized if you withdraw. ")]),_c('p',{staticClass:"text-left"},[_vm._v(" If you have any lasting questions about the research, please email me (Sumaiya Muhammad) at "),_c('a',{attrs:{"href":"#"}},[_vm._v("smuhamm1@ramapo.edu")])]),_c('p',{staticClass:"text-left"},[_vm._v("Please close this page end this study.")])])}]

